.wrapper {
  position: relative;

  .header {
    height: func_respons(120, 90);
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;

    svg:first-child {
      width: func_prop_maket(122);
      height: auto;
    }

    nav {

      a.burger_nav_link:not(:first-child) {
        margin-left: func_prop_maket(40);
      }
    }

    nav.open_nav {
      visibility: visible;
    }

    nav.close_nav {
      visibility: hidden;
    }

    .burger_nav {
      width: 24px;
      height: 24px;

      .nav_control {
        width: 100%;
        height: 100%;
        position: relative;

        .nav_btn {
          width: 100%;
          height: 100%;
          @include сentering-xy();
          overflow: hidden;
        }

        .nav_btn.btn_open {
          .burger_nav_line:first-child {
            @include сentering-xy();
            width: 110%;
            height: 2px;
            background-color: $brown;

            &::before {
              content: ' ';
              position: absolute;
              width: 110%;
              height: 2px;
              background-color: inherit;
              top: -300%;
              left: -1px;
            }

            &::after {
              content: ' ';
              position: absolute;
              width: 110%;
              height: 2px;
              background-color: inherit;
              top: 300%;
              left: -1px;
            }
          }

          .burger_nav_line:last-child {
            visibility: hidden;
          }
        }

        .nav_btn.btn_close {
          .burger_nav_line:first-child {
            @include сentering-xy();
            width: 110%;
            height: 2px;
            background-color: $brown;
            transform: translateX(-50%) rotateZ(45deg);
            -webkit-transform: translateX(-50%) rotateZ(45deg);
            -moz-transform: translateX(-50%) rotateZ(45deg);
            -ms-transform: translateX(-50%) rotateZ(45deg);
            -o-transform: translateX(-50%) rotateZ(45deg);
          }

          .burger_nav_line:last-child {
            @include сentering-xy();
            width: 110%;
            height: 2px;
            background-color: $brown;
            transform: translateX(-50%) rotateZ(-45deg);
            -webkit-transform: translateX(-50%) rotateZ(-45deg);
            -moz-transform: translateX(-50%) rotateZ(-45deg);
            -ms-transform: translateX(-50%) rotateZ(-45deg);
            -o-transform: translateX(-50%) rotateZ(-45deg);
          }
        }

        .nav_btn.btn_open:hover {
          .burger_nav_line:first-child {
            @include transit(0.5s);
            transform: translateX(-50%) rotateZ(90deg);
            -webkit-transform: translateX(-50%) rotateZ(90deg);
            -moz-transform: translateX(-50%) rotateZ(90deg);
            -ms-transform: translateX(-50%) rotateZ(90deg);
            -o-transform: translateX(-50%) rotateZ(90deg);
          }

          .burger_nav_line:last-child {
            visibility: hidden;
          }
        }

        .nav_btn.btn_close:hover {
          @include transit(0.5s);
          border: 1px solid $brown;
          @include bordeR(100%);
        }

        .nav_btn.btn_open:active>.burger_nav_line,
        .nav_btn.btn_close:active>.burger_nav_line {
          @include transit(0.1s);
          background-color: $red_dark;
        }
      }
    }

  }
}

