.testimonials_content {
  max-height: calc(160 + 216 + 160);
  width: fit-content;
  justify-items: space-between;
  text-align: left;
  padding: func_respons(160, 1) 0px func_respons(160, 1) func_respons(160, 1);

  overflow: hidden;

  .testimonial_case {
    min-width: max-content;

    max-height: fit-content;
    display: grid;
    grid-template-rows: minmax(216px, 1fr);
    grid-template-columns: repeat(4, minmax(197px, 1fr));


    .folower:not(:last-child) {
      margin-right: func_prop_maket(20);
    }

    .folower {
      min-width: 197px;
      height: 216px;
      max-height: fit-content;

      display: flex;
      flex-flow: column wrap;

      .folower_person {
        height: 40px;
        width: 100%;

        display: grid;
        grid-template-rows: 1fr;
        grid-template-columns: 40px 1fr;
        margin-bottom: func_prop_maket(24);

        .folower_foto {
          height: 40px;
          width: 40px;
          @include bordeR(100%);
          overflow: hidden;
          object-fit: cover;
          object-position: center center;

          img {
            height: auto;
            width: 40px;
          }
        }

        .folower_who {
          margin-left: func_prop_maket(16);
          display: flex;
          flex-flow: column nowrap;
          justify-content: space-around;
          align-items: flex-start;

          .folower_name {
            font-size: 12px;
            color: $brown_dark;
          }

          .folower_jobs {
            font-size: 7px;
            color: $beige_dark;
          }
        }
      }

      .folower_text {
        min-height: fit-content;
        font-size: 10px;
        line-height: func_rem(13);
        letter-spacing: 0.025rem;
        color: $beige_dark;
      }
    }
  }
}

.testimonials_content.show_vertical {
  min-height: fit-content;
  padding: func_respons(80, 1) 0px;

  .testimonial_case.show_vertical {
    width: 100%;
    min-height: fit-content;
    height: auto;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;

    .folower:not(:last-child) {
      margin-top: func_prop_maket(0);
    }

    .folower:not(:first-child) {
      margin-top: func_prop_maket(20);
    }

    .folower:not(:hover) {
      opacity: 0.5;

      .folower_text {
        height: 0px;
        overflow: hidden;
      }
    }

    .folower {
      min-width: 197px;
      max-width: 100%;
      min-height: func_prop_maket(40);
      height: min-content;
      max-height: fit-content;

      display: flex;
      flex-flow: column nowrap;
      justify-content: center;

      .folower_person {
        flex: 0 0 auto;
      }

      .folower_text {
        margin-bottom: func_prop_maket(10);
      }
    }
  }
}

.mod_video {
  position: relative;

  #mod_video {
    min-height: func_prop_maket(396);
    min-width: func_prop_maket(600);
  }

  svg:last-child {
    @include сentering-xy();


  }

  .display_none {
    display: none;
  }
}